<template>
  <div id="about">
    <sequential-entrance fromBottom>
      <div class="container">
        <h2>Welcome to our online school</h2>
        <div class="about_desc wow slideInLeft">
          <div class="about_left">
            <span>About school</span>
            <p>
              Online schools have virtual classes that students can take from
              home or a library or a coffee shop or anywhere else that has an
              Internet connection. The teachers work remotely and have a
              structured curriculum for their online students to follow.
            </p>
          </div>
          <div class="about_right">
            <a href="javascript:void(0)" @click="playVideo()">
              <div class="play"></div>
              <img
                src="https://img.youtube.com/vi/20Xkb7-2XtQ/maxresdefault.jpg"
                alt="1"
                style="width: 100%"
              />
            </a>
          </div>
        </div>
        <counter />
      </div>
    </sequential-entrance>
  </div>
</template>

<script>
import counter from "@/components/counter.vue";
export default {
  name: "About",
  components: {
    counter,
  },
  data() {
    return {
      videoHtml: "",
      videoCode: "20Xkb7-2XtQ",
    };
  },
  methods: {
    playVideo() {
      this.videoHtml = `<div class="video-container">
      <iframe width="560" height="315" src="https://www.youtube.com/embed/${this.videoCode}?autoplay=1&mute=1" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      </div>
      `;
      document.querySelector(".about_right").innerHTML = this.videoHtml;
    },
  },
};
</script>
